/* ===================== Global ===================== */
:root{
  --nav-h: 4rem;                 /* 고정 네비 높이 */

  /* 2페이지 비율(%) — 합이 100%를 넘지 않게! */
  --sec2-topP: 2%;               /* 위 스페이서 (작게 → 제목 더 위) */
  --sec2-titleP: 9%;             /* 제목 영역 */
  --sec2-tableP: 64%;            /* 표 영역 (크게) */
  --sec2-bottomP: 6%;            /* 아래 스페이서 */
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background: linear-gradient(to bottom, #fff8cc, #ffffff);
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  color: #2c3e50;
  text-align: center;
  overflow-x: hidden;
  overflow-y: scroll;
  scroll-snap-type: y mandatory;     /* 페이지 단위 스냅 */
  scroll-behavior: smooth;
  scroll-padding-top: var(--nav-h);  /* 스냅 정지점 보정 */
}

.fullpage-wrapper { width: auto; }

.page {
  min-height: 100svh;              /* 화면 꽉 + 내용 많으면 늘어남 */
  height: auto;
  scroll-snap-align: start;
  scroll-snap-stop: always;
  display: flex;
  flex-direction: column;
  justify-content: center;         /* 기본 가운데 정렬(섹션2는 따로) */
  align-items: center;
  box-sizing: border-box;
}

/* ===================== Intro Common ===================== */
.intro-section {
  min-height: 100svh;
  height: auto;
  padding: 4rem 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  scroll-margin-top: var(--nav-h);
}

.intro-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 3rem 3rem;
  border-radius: 2rem;
  text-align: center;
}

.subheading {
  font-family: 'EF_jejudoldam', sans-serif;
  font-weight: 600;
  font-size: 2.7rem;
  color: #24292e;
  margin: 0 0 1rem;
  text-align: center;
}

.intro-left-align { text-align: left !important; margin-left: 0 !important; padding-left: 0 !important; }

.intro-title {
  font-family: 'Cafe24Meongi', sans-serif;
  font-size: 2rem;
  margin-bottom: 0.5rem;
  color: #e74c3c;
}

.intro-text {
  font-family: 'S-CoreDream-3Light', sans-serif;
  line-height: 1.6;
  color: #2c3e50;
  margin: 2rem 0;
}

/* ===================== HERO (1페이지) ===================== */
.intro-hero{
  min-height: 100svh;
  display: grid;
  /* [윗공간][WELCOME][시루의 하루][이미지][설명][아랫공간] */
  grid-template-rows: 1fr auto auto auto auto 1fr;
  justify-items: center;
  padding: 0 16px;
}
.intro-hero .subheading{
  grid-row: 2;
  align-self: end;
  margin: 4px 0 0;
}
.intro-hero .intro-title{
  grid-row: 3;
  align-self: end;
  width: 100%;
  text-align: left;
  padding-left: 12px;
  margin: 0 0 8px;
}
.intro-banner-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  padding: 1rem 0;
}
.intro-hero .intro-banner-container{ grid-row: 4; margin: 0; }
.intro-banner{
  width: 100%;
  height: auto;
  display: block;
  object-fit: contain;
  margin: 0; padding: 0; border-radius: 0;
}
.intro-hero .intro-banner{ width: min(88vw, 418px); }
.intro-hero .intro-desc{
  grid-row: 5;
  align-self: start;
  margin-top: 24px;
  text-align: center;
  font-family: 'S-CoreDream-3Light', sans-serif;
  color: #373738ff;
  font-size: 1.1rem;
}

/* ===================== Speech Bubble ===================== */
.siru-speech-bubble {
  position: relative;
  display: inline-block;
  background: #ffffff;
  color: #2c3e50;
  border-radius: 999px;
  padding: 0.8rem 2rem;
  font-family: 'Cafe24Meongi', sans-serif;
  font-size: 1rem;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.1);
  max-width: 500px;
  animation: floatTalk 2s ease-in-out infinite;
  text-align: center;
}
.siru-speech-bubble::after {
  content: "";
  position: absolute;
  bottom: -12px; left: 50px; width: 0; height: 0;
  border-left: 12px solid transparent; border-right: 12px solid transparent; border-top: 12px solid #ffffff;
}
@keyframes floatTalk { 0%, 100% { transform: translateY(0); } 50% { transform: translateY(-4px); } }

/* ===================== Instagram ===================== */
.instagram-section { padding: 2rem 1rem; text-align: center; background: #fffdf4; }
.instagram-section h4 { font-family: 'MonoplexKR-Regular', sans-serif; margin-bottom: 1rem; color: #2c3e50; }
.instagram-link-new a {
  display: inline-flex; align-items: center; gap: 0.5rem;
  padding: 0.75rem 1.2rem; margin-top: 0; border: 1.5px solid #e1306c; border-radius: 6px;
  color: #e1306c; font-weight: 600; font-size: 1rem; font-family: 'S-CoreDream-6Bold', sans-serif;
  transition: all 0.3s ease; text-decoration: none; box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
}
.instagram-link-new a:hover { background: #e1306c; color: #ffffff; box-shadow: 0 4px 12px rgba(225, 48, 108, 0.2); }
.instagram-icon { width: 1.3rem; height: 1.3rem; }

/* ===================== Dog Info Table (기본) ===================== */
.dog-profile-title {
  font-size: 1.5rem;
  font-family: 'CoreDream', sans-serif;
  color: #141414;
  margin: 0 0 0.6rem;      /* 제목 더 위로 */
  text-align: center;
}
.dog-specs {
  width: 100%;
  background: #fff0f5;
  border-radius: 1.5rem;
  border: 2px solid #ffc4d6;
  box-shadow: 0 4px 16px rgba(255, 105, 180, 0.15);
  overflow: hidden;
  font-family: 'S-CoreDream-3Light', sans-serif;
  border-spacing: 0;
  margin: 0 auto;
}
.dog-specs th {
  background: #ffe2ec;
  color: #b23a48;
  padding: 1rem;
  font-weight: bold;
  font-size: 1rem;
  text-align: left;
  border-bottom: 1px dashed #fcb6c5;
}
.dog-specs td {
  background: #ffffff;
  color: #333;
  padding: 1rem;
  font-size: 0.95rem;
  border-bottom: 1px dashed #fcb6c5;
}
.dog-specs tr:last-child th,
.dog-specs tr:last-child td { border-bottom: none; }

/* ================================================================== */
/* ===================== SECTION 2 (중앙 + %비율 + 표 비례 축소) ===================== */
.intro-section.section-2 { padding-left: 0; padding-right: 0; } /* 좌우 패딩 비대칭 방지 */

/* 섹션 높이를 명확히: 퍼센트 행이 정상 계산되도록 */
.section-2{
  display: grid;
  height: calc(100svh - var(--nav-h));
  min-height: calc(100svh - var(--nav-h));
  margin-top: var(--nav-h);
  padding: 0;
  place-items: stretch;
  justify-content: center;
  justify-items: center;
}

/* 7행 그리드 — 표 행은 최소 높이 보장(사라짐 방지) */
.section-2 .sec2-grid{
  height: 100%;
  width: min(800px, 100%);
  margin-inline: auto;
  padding-inline: clamp(12px, 3vw, 20px);
  row-gap: 10px;
  box-sizing: border-box;
  justify-self: center;

  display: grid;
  grid-template-rows:
    var(--sec2-topP)                    /* 위 스페이서(%) */
    var(--sec2-titleP)                  /* 제목(%) */
    minmax(220px, var(--sec2-tableP))  /* ★ 표(%) + 최소 220px */
    auto                                /* misc(내용만큼) */
    var(--sec2-bottomP)                 /* 아래 스페이서(%) */
    min-content                         /* 푸터 */
    1rem;                               /* 바닥 여백 */
}

/* 자식들이 줄어들 수 있게(축소/스크롤 정상동작) */
.section-2 .sec2-grid > * { min-height: 0; }

/* 명시적 배치 */
.sec2-title { grid-row: 2; }
.sec2-specs { grid-row: 3; }
.sec2-misc  { grid-row: 4; }
.section-2 > .sec2-grid > footer {
  grid-row: 6;
  align-self: end;
  width: 100%;
  padding: 0;
}

/* === 표 래퍼: 중앙 정렬 + 비례 축소 컨테이너 === */
.sec2-specs{
  overflow: hidden;
  container-type: size;              /* cqh/cqw 사용 */
  display: grid;
  place-items: start center;
  padding: 8px;
}

/* 표 자체: 폭 제한 + 균등 열폭 */
.sec2-specs .dog-specs{
  width: min(600px, 100%);           /* 기본: 조금 크게 */
  margin-inline: auto;
  table-layout: fixed;
}
.sec2-specs .dog-specs th,
.sec2-specs .dog-specs td{ width: 50%; }

/* 컨테이너 높이에 비례해서 글자/패딩/행높이 스케일 */
.sec2-specs .dog-specs,
.sec2-specs .dog-specs th,
.sec2-specs .dog-specs td{
  font-size: clamp(12px, 2cqh, 17px);
}
.sec2-specs .dog-specs th{
  padding-block: clamp(6px, 1.3cqh, 16px);
  padding-inline: clamp(10px, 2.2cqw, 18px);
}
.sec2-specs .dog-specs td{
  padding-block: clamp(6px, 1.3cqh, 16px);
  padding-inline: clamp(10px, 2.2cqw, 18px);
}
.sec2-specs .dog-specs tr{
  height: clamp(32px, 4.8cqh, 60px);
}

/* misc: 내용만큼, 넘치면 내부 스크롤(필요시 유지) */
.sec2-misc{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: .6rem;
}

/* ===== 컨테이너쿼리 미지원 브라우저 폴백 ===== */
@supports not (height: 1cqh){
  .sec2-specs{ overflow: auto; } /* 구형은 스크롤로 폴백 */
  .sec2-specs .dog-specs{ width: min(600px, 100%); }
  .sec2-specs .dog-specs,
  .sec2-specs .dog-specs th,
  .sec2-specs .dog-specs td{ font-size: 15px; }
}

/* ===================== Responsive: Phone (<=480px) ===================== */
@media (max-width: 480px) {
  .intro-section { padding: 0 16px; }
  .intro-section.section-2 { padding-left: 0; padding-right: 0; }
  .intro-container { padding: 32px 0 0; }

  .intro-title { font-size: clamp(24px, 6vw, 32px); line-height: 1.2; }
  .intro-hero .subheading{
    grid-row: 2; align-self: start; margin-top: 0;
    transform: translateY(-12vh); /* 서브헤딩만 위로 */
  }
  .intro-hero .intro-banner{ width: min(88vw, 418px); }
  .intro-hero .intro-desc{ margin-top: 26px; }

  /* 2페이지 비율(모바일) — 표 더 큼, 제목 더 위 */
  :root{
    --sec2-topP: 1.2%;
    --sec2-titleP: 8%;
    --sec2-tableP: 70%;
    --sec2-bottomP: 5%;
  }

  .sec2-specs .dog-specs{ width: 100%; } /* 모바일은 꽉 차게 */
}

/* ===================== Responsive: Large Phone (481–600px) ===================== */
@media (min-width: 481px) and (max-width: 600px) {
  .intro-section { padding: 0 20px; }
  .intro-section.section-2 { padding-left: 0; padding-right: 0; }
  .intro-container { padding: 40px 0 0; }

  .intro-hero .subheading{
    grid-row: 2; align-self: start; margin-top: 0;
    transform: translateY(-9vh);
  }
  .intro-title { font-size: clamp(26px, 4.8vw, 40px); }
  .intro-hero .intro-banner{ width: min(77vw, 462px); }
  .intro-hero .intro-desc{ margin-top: 30px; }

  /* 2페이지 비율(라지폰) */
  :root{
    --sec2-topP: 1.5%;
    --sec2-titleP: 9%;
    --sec2-tableP: 68%;
    --sec2-bottomP: 5.5%;
  }
}

/* ===================== 짧은 화면(가로 등) 보정 ===================== */
@media (max-height: 700px) {
  :root{
    --sec2-topP: 1%;
    --sec2-titleP: 9%;
    --sec2-tableP: 66%;
    --sec2-bottomP: 6%;
  }
}

/* ===================== Safety ===================== */
img, video { max-width: 100%; height: auto; display: block; }
