.gallery-page {
  padding: 5rem 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

/* 🧸 헤더 */
.gallery-header {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  justify-content: center;
  margin-bottom: 0.5rem;
}
.gallery-title {
  font-size: 1.5rem;
  font-weight: 800;
  padding: 1rem 1rem 0.5rem;
  text-align: center;
  color: #222;
  line-height: 1.4;
}
.gallery-icon { font-size: 1.6rem; }
.gallery-description {
  text-align: center;
  font-size: 1rem;
  color: #666;
  margin-bottom: 2rem;
}

/* 📋 필터 */
.gallery-filter {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin: 1.5rem 0;
  flex-wrap: wrap;
}
.gallery-filter select {
  appearance: none;
  background-color: #fff7f0;
  border: 2px solid #ffb6c1;
  border-radius: 1rem;
  padding: 0.6rem 1.2rem;
  font-size: 1rem;
  font-family: 'HancomMalangMalang-Regular', sans-serif;
  color: #ff5d8f;
  box-shadow: 2px 2px 6px rgba(255, 182, 193, 0.2);
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
}
.gallery-filter select:hover {
  background-color: #ffe4ec;
  border-color: #ff85a2;
  transform: scale(1.05);
}
.gallery-filter select:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(255, 192, 203, 0.4);
}

/* 📷 갤러리 그리드 — 모바일 1열 / 태블릿 2열 / 데스크탑 3열 */
.gallery-gallery {
  display: grid;
  grid-template-columns: 1fr;     /* 기본(모바일): 1열 */
  gap: 1.8rem;
  padding: 0 1rem;
}
@media (min-width: 600px) {
  .gallery-gallery { grid-template-columns: repeat(2, 1fr); } /* 2열 */
}
@media (min-width: 1024px) {
  .gallery-gallery { grid-template-columns: repeat(3, 1fr); } /* 3열 */
}

/* 📦 카드 */
.gallery-card {
  background: #ffffffd9;
  border-radius: 1.2rem;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.06);
  overflow: hidden;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  backdrop-filter: blur(2px);
  padding: 0; /* 카드 비율/구조는 유지 */
}
.gallery-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 28px rgba(0, 0, 0, 0.08);
}

/* ⬆️ 제목 */
.gallery-title-card {
  font-family: 'HancomMalangMalang-Regular';
  font-size: 1.5rem;
  font-weight: 800;
  padding: 1rem 1rem 0.5rem;
  text-align: center;
  color: #222;
  line-height: 1.4;
}

/* 🖼 이미지 컨테이너: 날짜 위, 사진 아래 */
.gallery-media{
  position: relative;  /* 기준 */
  padding: 0.75rem 0.9rem 0.6rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* 날짜: 오버레이 없이, 사진 ‘위’ + 우측 정렬 */
.gallery-media .gallery-date{
  font-family: 'HancomMalangMalang-Regular';
  position: static;
  width: 80%;          /* 사진 폭과 맞추기 */
  align-self: center;
  text-align: right;
  margin: 0 0 8px;     /* 날짜와 사진 사이 간격 */
  padding: 0;
  font-size: 0.82rem;
  color: #666;
  font-style: italic;
  background: none;
  border: 0;
  box-shadow: none;
}

/* 사진: 카드 안쪽으로 축소 */
.gallery-media .gallery-image{
  width: 92%;
  aspect-ratio: 3 / 3.7;
  object-fit: cover;
  display: block;
  margin: 0 auto;
  border-radius: 0.75rem;
}

/* ✅ 본문 텍스트 (사진 아래) */
.gallery-body{
  font-family: 'HancomMalangMalang-Regular';
  font-size: 1rem;
  padding: 0.8rem 1rem 1.2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.gallery-text{
  font-size: 0.93rem;  /* 텍스트만 살짝 줄임 */
  color: #444;
  text-align: center;
  line-height: 1.6;
}

/* 📱 모바일 추가 미세조정 */
@media (max-width: 600px) {
  .gallery-gallery { padding: 0; }
  .gallery-media{ padding: 0.6rem 0.7rem 0.5rem; }
  .gallery-media .gallery-date{ width: 85%; margin-bottom: 6px; font-size: 0.85rem; }
  .gallery-media .gallery-image{ width: 94%; border-radius: 0.6rem; }
  .gallery-text { font-size: 1.2rem; }

}
